.middle {
  position: fixed;
  left: 0;
  top: 0;
  .content {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    font-size: 20px;
    text-align: center;
    padding-top: 12%;
   .middle-logo {
      width: 220px;
      border-radius: 50%;
    }
    .middle-header {
      .link {
        display: block;
      }
    }
     .middle-body {
      padding-top: 20px;
      .list {
        .link {
          display: inline-block;
          padding: 20px;
          color: #ffffff;
          min-width: 80px;
        }
        .link:hover {
          color: rgb(0, 243, 255);
        }
      }
    }
    /*.introduce{
      color: #960026;
    }*/
  }
}
