@import url(//at.alicdn.com/t/font_1760623_n2bxubkx5li.css);
[class^="icon-"]{font-family:"iconfont" !important;font-style:normal;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.el-menu-item [class^=icon-]{margin-right:5px;width:24px;text-align:center;font-size:18px;vertical-align:middle}.el-submenu [class^=icon-]{vertical-align:middle;margin-right:5px;width:24px;text-align:center;font-size:18px}@-webkit-keyframes iconAnimate{0%,100%{transform:scale(1)}10%,30%{transform:scale(0.5)}20%,40%,60%,80%{transform:scale(1.5)}50%,70%{transform:scale(1.5)}}@keyframes iconAnimate{0%,100%{transform:scale(1)}10%,30%{transform:scale(0.5)}20%,40%,60%,80%{transform:scale(1.5)}50%,70%{transform:scale(1.5)}}html,body,ul,li{margin:0;padding:0}html body{font-family:sans-serif;color:#333}html body a{cursor:pointer}a{text-decoration:none;color:#555}h1,h2,h3,h4,h5,h6{font-weight:bold}::-webkit-scrollbar{width:8px;height:8px}::-webkit-scrollbar-thumb{background-color:rgba(144,147,153,0.3);border-radius:4px}

.layouts-footer{padding:20px 0;font-size:14px;text-align:center;background:#333;color:#a5a4a4}.layouts-footer--love{display:inline-block;margin:0 15px;-webkit-animation:iconAnimate 1.5s ease-in-out infinite;animation:iconAnimate 1.5s ease-in-out infinite;color:#FF0000}

@media screen and (max-width: 780px){.nav{padding:0 10px !important}.nav-row{width:100% !important}.ant-layout-content{padding:0 10px !important}.Layouts .home-slider{display:none !important}.article-side{display:none !important}}

.nav{background-color:#fff;padding:0 calc(50% - 600px)}.nav-logo{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;display:flex;align-items:center}.nav-logo--img{width:40px;height:40px;border-radius:50%;margin-right:20px}.nav-logo--name{font-size:24px;color:#000}.nav-menu{display:flex}.nav-menu--item{font-size:16px;padding:10px 20px;height:64px;list-style:none}.nav-menu--item a{display:flex;align-items:center;height:44px;border-bottom:2px solid;border-color:transparent}.nav-menu--item .active{color:#000;border-color:#333}

.middle{position:fixed;left:0;top:0}.middle .content{position:fixed;left:0;top:0;right:0;bottom:0;font-size:20px;text-align:center;padding-top:12%}.middle .content .middle-logo{width:220px;border-radius:50%}.middle .content .middle-header .link{display:block}.middle .content .middle-body{padding-top:20px}.middle .content .middle-body .list .link{display:inline-block;padding:20px;color:#ffffff;min-width:80px}.middle .content .middle-body .list .link:hover{color:#00f3ff}

